import React from 'react';
import {createUseStyles} from 'react-jss';

const useStyles = createUseStyles({
  root: {
    display: 'flex',
  },
});

const Projects = () => {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <div>Project 1</div>
      <div>Project 2</div>
    </div>
  );
};

export default Projects;
